	module.exports = {"data":{"site":{"siteMetadata":{"title":"weiran.co","author":"Weiran Zhang"}},"markdownRemark":{"id":"/Users/weiran/git/weiran/weiran.co/src/pages/blog/2012/11/stop-fixing-font-smoothing/index.md absPath of file >>> MarkdownRemark","html":"<p>Dmitry Fadeyev on the practice of changing the default font smoothing for WebKit for no reason other than misguided aesthetics:</p>\n<blockquote>\n<p>Designers, having received a new property to play with, began to use the antialiasing mode everywhere on their text, even for large portions of dark text on light backgrounds. Disabling subpixel rendering made that text look different, more slender and without any hint of subpixel discoloration. Because of this, at first glance, antialiased text appears to look better than its subpixel sibling.</p>\n<p>But upon closer inspection, antialiased text is always blurrier than subpixel rendered text. This is not a matter of opinion, it’s just how the rendering works. Subpixel rendering utilizes each of the three individual subpixels in every pixel of the screen to smooth out and sharpen the edges of the font. Antialiasing relies on whole pixels only, which effectively reduces its smoothing sharpness by three times.</p>\n</blockquote>","frontmatter":{"title":"Stop \"fixing\" font smoothing","date":"November 15, 2012","passthroughUrl":"http://www.usabilitypost.com/2012/11/05/stop-fixing-font-smoothing/"}}},"pathContext":{"slug":"/blog/2012/11/stop-fixing-font-smoothing/","previous":{"fields":{"slug":"/blog/2012/11/no-compromises/"},"frontmatter":{"title":"No compromises","passthroughUrl":"http://parislemon.com/post/35654572810/the-sin-ofsky-s-of-the-father","type":null}},"next":{"fields":{"slug":"/blog/2012/11/the-bond-villains-lair-skyfalls-abandoned-island/"},"frontmatter":{"title":"The Bond villain's lair: Skyfall's abandoned island","passthroughUrl":"http://www.messynessychic.com/2012/10/29/the-bond-villains-lair-skyfalls-abandoned-island/","type":null}}}}

